
import {
    Switch,
    useHistory
} from "react-router-dom"

import { AuthProvider, PrivateRoute } from "@meddbriefer/mdb-auth";
import { useFirebaseProject, FirebaseContext } from "@meddbriefer/mdb-firebase"

import {GetReview, UnauthedLayout} from "@meddbriefer/cond1-debriefing-ui"
import { LogChooser } from "@meddbriefer/log-chooser"
import { environment } from "./environments/environment";


const App = (props) => {
    const {auth, db } = useFirebaseProject(environment.firebaseProject)
    let history = useHistory()
    window.db = db;

    function redirectToCond1Debriefing(debriefID) {
      const newURL = `/debriefing/${debriefID}`
      history.push(newURL)
    }

    return (
      <FirebaseContext.Provider
        value={{
            auth,
            db
        }}
      >
        <AuthProvider>
            <Switch>
              {/* <PrivateRoute
                exact
                path="/logs/:logID"
                authFlowLayout={UnauthedLayout}
                component={AnalyzeDebriefing}
              />
              
             
              <PrivateRoute
                exact
                path="/analysis/startReview/:logID"
                authFlowLayout={UnauthedLayout}
                component={InitializeForReview}
              /> */}
              <PrivateRoute
                exact
                path="/debriefing/:logID"
                authFlowLayout={UnauthedLayout}
                component={GetReview}
              />

              <PrivateRoute authFlowLayout={UnauthedLayout}>    
                <LogChooser
                  redirectToCond1Debriefing={redirectToCond1Debriefing} 
                />
              </PrivateRoute>
              
              {/*default (catchall) route*/}
              {/* <PrivateRoute
                match
                path="/"
                authFlowLayout={UnauthedLayout}
                component={LogList}
              /> */}
            </Switch>
        </AuthProvider>
      </FirebaseContext.Provider>
    )
}


export default App;